@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;0,900;1,400;1,900&display=swap);
*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body{
    font-family: 'Lato', sans-serif;
}



p{
    text-align: justify;
    font-family: sans-serif;
}

.about{
    margin-top:100px;
}

.about .img_section{
    border:8px solid crimson;
    overflow: hidden;
}
img{
    transition: 0.5s;
}
.about .img_section img:hover{
    -webkit-transform: rotate(10deg) scale(1.3);
            transform: rotate(10deg) scale(1.3);
}
.about .right_section h1{
    font-family: 'lato';
}
.about .right_section h5{
    font-family: sans-serif;
}
.primary_btn {
    text-decoration: none;
    padding: 10px 15px;
    background-color: cornflowerblue;
    color: #fff;
    border-radius: 5px;
    transition: 0.4s;
    border:none;
    cursor: pointer;
}

.primary_btn:hover {
    box-shadow: 7px 4px 5px 0px rgba(0, 0, 0, 0.27);
    -webkit-box-shadow: 7px 4px 5px 0px rgba(0, 0, 0, 0.27);
    -moz-box-shadow: 7px 4px 5px 0px rgba(0, 0, 0, 0.27);
    
}
.primary_btn:active{
    background-color:#fff;
    color:cornflowerblue;
}

.secondary_btn {
    text-decoration: none;
    padding: 10px 15px;
    background-color: #111;
    color: #fff;
    border-radius: 5px;
    transition: 0.4s;
    border:none;
    cursor: pointer;
}

.secondary_btn:hover {
    box-shadow: 7px 4px 5px 0px rgba(0, 0, 0, 0.27);
    -webkit-box-shadow: 7px 4px 5px 0px rgba(0, 0, 0, 0.27);
    -moz-box-shadow: 7px 4px 5px 0px rgba(0, 0, 0, 0.27);
    
}
.secondary_btn:active{
    background-color:cornflowerblue;
    color:#fff;
}
